.page-header {
  position: relative;

  &::before {
    position: absolute;
    content: "";
    display: block;
    height: 100%;
    max-height: 239px;

    left: 0;
    right: 0;
    bottom: 0;

    background-image: url("../img/bg-back-mobile@1x.jpg");
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-back-mobile@2x.jpg");
    }
  }

  &--main {
    &::before {
      max-height: 734px;
      background-position: center -45px;
    }
  }
}

.page-header__wrapper {
  width: 100%;

  background-color: rgba(0, 0, 0, 0.3);
  opacity: 0.99;
}


.page-header__inner {
  width: 100%;

  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1;

  &--opened {
    background-color: @base-color;
  }
}

.page-header__logo {
  display: flex;
  align-items: baseline;
  width: 75px;

  padding-top: 22px;
  padding-bottom: 18px;
  margin-left: 20px;
}

.page-header__logo-image {
  width: 75px;
  height: 22px;
}

.page-header__intro {
  z-index: 2;
  opacity: 0.99;
}

.page-header__title {
  display: block;
  width: 250px;

  padding-top: 30px;
  padding-bottom: 80px;
  margin: 0 auto;

  .title();
  text-align: center;
  line-height: 30px;
  color: @base-light-color;

  &--form {
    padding-bottom: 50px;
  }
}

@media (min-width: @tablet-width) {
  .page-header {
    &::before {
      max-height: 438px;
      background-image: url("../img/bg-back-tablet@1x.jpg");
      background-position: center top;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/bg-back-tablet@2x.jpg");
      }
    }

    &--main {
      &::before {
        max-height: 734px;
      }
    }
  }

  .page-header__logo {
    width: 147px;

    padding-top: 28px;
    padding-bottom: 35px;
  }

  .page-header__logo-image {
    width: 147px;
    height: 40px;
  }

  .page-header__title {
    width: 500px;
    padding-top: 68px;
    padding-bottom: 140px;

    font-size: 50px;
    line-height: 60px;

    &--form {
      padding-bottom: 80px;
    }
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    &::before {
      max-height: 395px;
      background-image: url("../img/bg-back-desktop@1x.jpg");
      background-position: center top;
      background-repeat: no-repeat;
      background-size: cover;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/bg-back-desktop@2x.jpg");
      }
    }

    &--main {
      &::before {
        max-height: 854px;
      }
    }
  }

  .page-header__wrapper {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .page-header__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .desktop-wrapper();
    padding-top: 28px;
    padding-bottom: 35px;
    background-color: transparent;
  }

  .page-header__logo {
    margin-left: 0;
    padding-top: 0;
    padding-bottom: 0;
  }

  .page-header__title {
    width: 550px;
    padding-top: 60px;
    padding-bottom: 104px;

    &--form {
      width: 800px;
    }
  }
}
